import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import { conCotizacion } from '../../Models/Factura';
import Seguimientos from '../../Utiles/Componentes/Seguimiento';
import { zfill } from '../../Utiles/Formatos';
import FilaVentas from '../../Utiles/Ventas/FilaVentas';
import ModalDetalle from './ModalDetalle';


export default class FilaCotizacio extends FilaVentas {
    con = conCotizacion();
    print = true;
    constructor(p: any) {
        super(p);
        this.Label = 'COTIZACI√ìN';
        this.tipo = 'Cotizacion';
    }
    getModal() {
        let { codigo } = this.props.data;
        let { aux, detalles } = this.state;
        return <>
            <Seguimientos
                key={'Seguimientos' + this.tipo + codigo} type='Cotizacion'
                id={codigo} close={this.close.bind(this)} open={aux} />
            <ModalDetalle open={detalles} id={codigo} close={this.close.bind(this)}
                titulo={'DETALLES DE COTIZACION N¬∞ ' + zfill(codigo)} />
        </>
    }
    getButtons() {
        return <ButtonGroup className="float-right">
            <Button data-tip="VER SEGUIMIENTO" variant="outline-dark"
                onClick={() => { this.setState({ aux: true }) }} >
                <i className="fa fa-eye"></i>
            </Button>
            <Button data-tip="VER IMPRECION" variant="primary"
                onClick={() => { this.setState({ print: true }) }}>
                <i className="fa fa-download"></i>
            </Button>
            {(this.props.data.status === 1) ?
                <Button data-tip="CANCELAR" variant="danger"
                    onClick={() => { this.setState({ eliminar: true }) }} >
                    <i className="fa fa-ban"></i>
                </Button> : <></>}
        </ButtonGroup>
    }
    cancelado() {
        return 'COTIZACION N¬∞ ' + this.props.data.codigo + ' CANCELADA ESXITOSAMENTE';
    }
}