
import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import { conFactura } from '../../Models/Factura';
import FilaVentas from '../../Utiles/Ventas/FilaVentas';
import ModalPrint from '../../Utiles/Componentes/ModalReport';
import {zfill} from '../../Utiles/Formatos';
export default class FilaFactura extends FilaVentas {
    con=conFactura();
    constructor(p: any) {
        super(p);
        this.Label = 'FACTURA';
        this.tipo = 'Factura';
    }
    getModal() {
        let { codigo } = this.props.data;
        return <ModalPrint title={"IMPRIMIR " + this.Label + " N¬∞ " + zfill(codigo)}
            url={'/api/Factura/PDF/'+codigo}
            key={'PrintDolar' + this.tipo + codigo}
            open={this.state.print} close={this.close.bind(this)} />
    }
    getButtons() {
        return <ButtonGroup className="float-right">
            <Button data-tip="VER IMPRECION" variant="primary"
                onClick={() => { this.setState({ print: true }) }}>
                <i className="fa fa-download"></i>
            </Button>
            {(this.props.data.status === 2) ?
                <Button data-tip="CANCELAR" variant="danger"
                    onClick={() => { this.setState({ eliminar: true }) }} >
                    <i className="fa fa-ban"></i>
                </Button> : <></>}
        </ButtonGroup>
    }
    getStatus() {
        if (this.props.data.status === 2)
            return <span className="badge badge-success">FACTURADA</span>
        return <span className="badge badge-danger">CANCELADA</span>;
    }
    cancelado() {
        return 'FACTURA N¬∞ ' + this.props.data.codigo + ' CANCELADA ESXITOSAMENTE';
    }
}